/* HARA-XY Professional Theme - Metallic Silver & Black */

/* Custom Colors */
:root {
  --color-black: #000000;
  --color-dark-black: #0a0a0a;
  --color-silver: #c0c0c0;
  --color-light-silver: #e0e0e0;
  --color-dark-silver: #8a8a8a;
  --color-gray: #444444;
  --color-light-gray: #666666;
  --color-white: #ffffff;
}

/* Base Styles */
* {
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  background: var(--color-black);
  color: var(--color-silver);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Custom Utility Classes */
.text-silver {
  color: var(--color-silver);
}

.text-light-silver {
  color: var(--color-light-silver);
}

.text-dark-silver {
  color: var(--color-dark-silver);
}

.bg-silver {
  background-color: var(--color-silver);
  /* background-color: var(--color-black); */
}

.bg-dark-silver {
  background-color: var(--color-dark-silver);
}

.border-silver {
  border-color: var(--color-silver);
}

/* Typography */
.font-mono {
  font-family: 'JetBrains Mono', 'Fira Code', 'Consolas', monospace;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes glow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(192, 192, 192, 0.3);
  }
  50% {
    box-shadow: 0 0 40px rgba(192, 192, 192, 0.6);
  }
}

.animate-fade-in-up {
  animation: fadeInUp 0.8s ease-out forwards;
}

.animate-slide-in-left {
  animation: slideInLeft 0.8s ease-out forwards;
}

.animate-slide-in-right {
  animation: slideInRight 0.8s ease-out forwards;
}

.animate-glow {
  animation: glow 2s ease-in-out infinite;
}

/* Custom Components */
.gradient-text {
  background: linear-gradient(135deg, var(--color-light-silver), var(--color-silver));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.glass-morphism {
  background: rgba(192, 192, 192, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(192, 192, 192, 0.2);
}

.metallic-gradient {
  background: linear-gradient(135deg, #c0c0c0, #888888, #c0c0c0);
}

.card-hover-effect {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-hover-effect:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 60px rgba(192, 192, 192, 0.15);
}

/* Interactive Elements */
.btn-primary {
  background: linear-gradient(135deg, var(--color-silver), var(--color-light-silver));
  color: var(--color-black);
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(192, 192, 192, 0.4);
  background: linear-gradient(135deg, var(--color-light-silver), var(--color-white));
}

.btn-secondary {
  background: transparent;
  color: var(--color-silver);
  border: 2px solid var(--color-silver);
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-secondary:hover {
  background: var(--color-silver);
  color: var(--color-black);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(192, 192, 192, 0.3);
}

/* Loading Animation */
.loading-spinner {
  border: 3px solid var(--color-gray);
  border-top: 3px solid var(--color-silver);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .responsive-text {
    font-size: clamp(1.5rem, 4vw, 3rem);
  }
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-dark-black);
}

::-webkit-scrollbar-thumb {
  background: var(--color-dark-silver);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-silver);
}

/* Selection Color */
::selection {
  background: var(--color-silver);
  color: var(--color-black);
}

/* Focus Styles */
.focus-outline:focus {
  outline: 2px solid var(--color-silver);
  outline-offset: 2px;
}

/* Grid Patterns */
.grid-pattern {
  background-image: 
    linear-gradient(rgba(192, 192, 192, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(192, 192, 192, 0.1) 1px, transparent 1px);
  background-size: 20px 20px;
}

/* Professional shadows */
.professional-shadow {
  box-shadow: 
    0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06),
    0 0 0 1px rgba(192, 192, 192, 0.1);
}

.professional-shadow-lg {
  box-shadow: 
    0 25px 50px -12px rgba(0, 0, 0, 0.25),
    0 0 0 1px rgba(192, 192, 192, 0.1);
}